<script>
import axios from "axios";

export default {
  data() {
    return {
      departments: [],
    };
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/departments/fetch",
      params: {
        with_responsibilities: 1,
        assigned: 1,
      },
      success: (response) => {
        this.departments = response.result.data;
      },
      error: (error) => {
        console.log(error);
      },
    });
  },
};
</script>
